import { LOGOUT, LOGOUT_SUCCESS } from '../types';
import { API } from '../../constants';

export const logout = () => ({
  type: LOGOUT,
  meta: {
    type: 'api',
    url: `${API}admin/logout`,
    method: 'post',
    body: JSON.stringify({})

  },
  success: { action: (response) => logoutSuccess(response) }
})

export const logoutSuccess = () => {
  localStorage.clear();


  return {
    type: LOGOUT_SUCCESS,
    meta: {
      type: 'navigateAway',
      redirectTo: `${API}admin`
    }
  }
}



