import React, { Component, Fragment } from 'react'
import { Route, Switch } from 'react-router-dom'
import Header from './components/Header'
import * as Button from './ui'
import SideNav from './components/SideNav'
import PrimaryContent from './containers/PrimaryContent'
import Dashboard from './containers/Dashboard'
import NewPlan from './containers/plan/NewPlan'
import PlanDetails from './containers/plan/PlanDetails'
import PlanList from './containers/plan/PlanList'
import OrderList from './containers/orders/OrderList'

class Layout extends Component {
  render() {
    return (
      <Fragment>
        <SideNav />
        <div className="main-content">
          <Header />
          <PrimaryContent />
          {/* <Header></Header>
          <Button.Warning
            text='refresh'
            onClick={() => {
              window.location.reload()
            }}
          /> */}
          <Switch>
            <Route path="/admin/home/dashboard" component={Dashboard} />
            <Route exact path="/admin/home/plans" component={PlanList} />
            <Route exact path="/admin/home/plans/create" component={NewPlan} />
            <Route path="/admin/home/plans/:id" component={PlanDetails} />
            <Route exact path="/admin/home/orders" component={OrderList} />
          </Switch>
        </div>
      </Fragment>
    )
  }
}
export default Layout



