import { push } from 'connected-react-router';
import { API } from '../../constants';

const DEFAULT_HEADERS = { 'Accept': 'application/json', 'Content-Type': 'application/json' };



export const apiMiddleware = store => next => action => {

  if (!action.meta || action.meta.type !== 'api') {
    if (action.meta) {
      if (action.meta.type) {

        switch (action.meta.type) {
          case 'redirect':
            store.dispatch(push(action.meta.redirectTo));
            break;
          case 'navigateAway':
            window.location.href = action.meta.redirectTo;
            break;
          default:
            break;
        }
      }

    }
    return next(action);
  }
  // This is an api request
  // Find the request URL and compose request options from meta
  const { url } = action.meta;

  const fetchOptions = {
    ...action.meta,
    headers: new Headers({
      ...DEFAULT_HEADERS,
      // 'Accept': 'application/json, text/plain, */*',

      // 'Content-Type': 'application/json',
      // ...action.meta.headers,
      'X-CSRF-TOKEN': document.querySelector("meta[name='csrf-token']").getAttribute("content")

      // $('meta[name="csrf-token"]').attr('content')

    })
  };



  // Make the request
  fetch(url, fetchOptions)
    // convert the response to json
    .then(resp => resp.json())
    .then(json => {
      // respond back to the user
      // by dispatching the original action without
      // the meta object
      // let newAction = Object.assign({}, action, {
      //   payload: json
      // });
      // delete newAction.meta;
      // store.dispatch(newAction);


      // test
      if (action.success) {
        if (action.success.action) {
          store.dispatch(action.success.action(json))
        }
        if (action.success.redirectTo) {
          store.dispatch(push(action.success.redirectTo))
        }
      }
    });
};

