import {
  SET_PLAN_DETAILS,
  CLEAR_PLAN_DETAILS,
  FETCH_PLAN_DETAILS
} from '../types'
import { API } from '../../constants'

export const setPlanDetails = plan => ({
  type: SET_PLAN_DETAILS,
  payload: plan
})

export const clearPlanDetails = () => ({
  type: CLEAR_PLAN_DETAILS,
  payload: {}
})

export const fetchPlanDetails = planId => ({
  type: FETCH_PLAN_DETAILS,
  payload: {},
  meta: {
    type: 'api',
    url: `${API}plans/${planId}`,
    method: 'GET'
  },
  success: {
    action: response => setPlanDetails(response),
    redirectTo: `/admin/home/plans/${planId}`
  }
})



