import { FETCH_ORDERS_SUCCESS } from '../types'

export const initialState = {}

export const reducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_ORDERS_SUCCESS:
      return action.payload.data
    default:
      return state
  }
}

export default reducer



