import { FETCH_ORDERS_SUCCESS, FETCH_ORDERS } from '../types'
import { API } from '../../constants'

export function fetchOrders(pageNumber = 1) {
  return {
    type: FETCH_ORDERS,
    payload: {},
    meta: {
      type: 'api',
      method: 'get',
      url: `${API}orders?page=${pageNumber}`
    },
    success: {
      action: response => fetchOrdersSuccess(response)
    }
  }
}

export function fetchOrdersSuccess(retrievedPlansData) {
  return {
    type: FETCH_ORDERS_SUCCESS,
    payload: retrievedPlansData
  }
}



