import React, { Fragment, Component } from 'react'
import { connect } from 'react-redux'
import {
  fetchPlans,
  deletePlan,
  clearPlanDetails,
  fetchCoreFeatures
} from '../../store/actions'
import './plan.styles.css'
import Plan from './Plan'
import { Link } from 'react-router-dom'

class PlanList extends Component {
  constructor(props) {
    super(props)
  }
  componentDidMount() {
    this.props.clearPlanDetails()
    this.props.fetchPlans()
    // Note: make sure if the below data once loaded, donot refetch
    if (!this.props.features.length) {
      this.props.fetchCoreFeatures()
    }
  }

  preparePlansList() {
    return (this.props.plans || []).map(plan => (
      <Plan key={plan.id} plan={plan} />
    ))
  }

  render() {
    return (
      <Fragment>
        <div className="container-fluid mt--7">
          <div className="row">
            <div className="card shadow">
              <div className="card-header border-0">
                <div className="row">
                  <div className="col-12 col-md-8">
                    <span>
                      <h3 className="mb-0">Plans</h3>
                    </span>
                  </div>
                  <div className="col-6 col-md-4">
                    <span>
                      <Link to={'/admin/home/plans/create'}>
                        <button
                          type="button"
                          className="btn btn-primary btn-sm add-plan"
                        >
                          Add New Plan
                        </button>
                      </Link>
                    </span>
                  </div>
                </div>
              </div>
              <div className="table-responsive">
                <table className="table align-items-center">
                  <thead className="thead-light">
                    <tr>
                      <th scope="col">Name</th>
                      <th scope="col">Price</th>
                      <th scope="col">Status</th>
                      <th scope="col">Users</th>
                      <th scope="col">Active Users</th>
                      <th scope="col" />
                    </tr>
                  </thead>
                  <tbody>{this.preparePlansList()}</tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </Fragment>
    )
  }
}
export default connect(
  state => ({
    plans: state.plans,
    features: state.features.coreFeatures || []
  }),
  { fetchPlans, deletePlan, clearPlanDetails, fetchCoreFeatures }
)(PlanList)



