import {
  FETCH_CORE_FEATURES_SUCCESS,
  SET_ACTIVE_FEATURE,
  CLEAR_ACTIVE_FEATURE
} from '../types'

export const initialState = { coreFeatures: [], selectedFeature: {} }

export const reducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_CORE_FEATURES_SUCCESS:
      return { ...state, coreFeatures: action.payload.data }
    case SET_ACTIVE_FEATURE:
      return { ...state, selectedFeature: action.payload }
    case CLEAR_ACTIVE_FEATURE:
      return { ...state, selectedFeature: {} }
    default:
      return state
  }
}

export default reducer



