import React, { Fragment } from 'react'
import { connect } from 'react-redux'
import { push } from 'connected-react-router'
import './feature.styles.css'
import { setActiveFeature, clearActiveFeature } from '../../store/actions'

class Feature extends React.Component {
  constructor(props) {
    super(props)
    this.editFeature = this.editFeature.bind(this)
  }

  editFeature(feature) {
    this.props.clearActiveFeature()
    this.props.setActiveFeature(feature)
  }

  render() {
    const { feature } = this.props
    return (
      <Fragment>
        <tr className={feature.deleted_at ? 'plan-deleted-status' : ''}>
          <th scope="row">
            <div className="media align-items-center">
              <span className="avatar rounded-circle mr-3 bg-warning">
                {feature.name.charAt(0)}
              </span>
              <div
                className={`media-body ${feature.deleted_at ? '' : 'cursor'}`}
                onClick={() => this.navigateToDetailPage(plan)}
              >
                <span className="mb-0 text-sm">{feature.name}</span>
              </div>
            </div>
          </th>
          <td>₹{feature.price}</td>
          <td>
            <span className="badge badge-dot mr-4">
              <i className="bg-warning" />{' '}
              {feature.deleted_at ? 'Deleted' : 'Active'}
            </span>
          </td>
          <td>1</td>
          <td>1</td>
          <td className="text-right">
            {!feature.deleted_at && (
              <div className="dropdown">
                <a
                  className="btn btn-sm btn-icon-only text-light"
                  href="#"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i className="fas fa-ellipsis-v" />
                </a>
                <div className="dropdown-menu dropdown-menu-right dropdown-menu-arrow">
                  <a
                    className="dropdown-item"
                    onClick={() => this.props.deleteFeature(feature.id)}
                  >
                    Delete
                  </a>

                  {/* <button
                    type="button"
                    className="btn btn-sm btn-warning ml-4"
                    data-toggle="modal"
                    data-target="#modal-form"
                  >
                    Edit
                              </button> */}

                  <a
                    data-toggle="modal"
                    data-target="#add-or-edit-feature-modal"
                    className="dropdown-item"
                    onClick={() => this.editFeature(feature)}
                  >
                    Edit
                  </a>
                </div>
              </div>
            )}
          </td>
        </tr>
      </Fragment>
    )
  }
}
export default connect(
  null,
  {
    push,
    setActiveFeature,
    clearActiveFeature
  }
)(Feature)



