import React, { Fragment } from 'react'
import { connect } from 'react-redux'
import { push } from 'connected-react-router'
import './order.styles.css'

class Order extends React.Component {
  constructor(props) {
    super(props)
    this.navigateToDetailPage = this.navigateToDetailPage.bind(this)
    this.state = {
      viewLogs: false
    }
  }

  toggleViewLogs() {
    this.setState((state, props) => ({
      viewLogs: !state.viewLogs
    }))
  }

  navigateToDetailPage(plan) {
    // if (!plan.deleted_at) {
    //   this.props.push(`/admin/home/plans/${plan.id}`)
    // }
  }
  render() {
    const { plan } = this.props
    return (
      <tr className={plan.deleted_at ? 'plan-deleted-status' : ''}>
        <th scope="row">
          <div className="media align-items-center">
            <span className="avatar rounded-circle mr-3 bg-warning">
              {plan.organization_name.charAt(0)}
            </span>
            <div
              className={`media-body ${plan.deleted_at ? '' : 'cursor'}`}
              onClick={() => this.navigateToDetailPage(plan)}
            >
              <span className="mb-0 text-sm">{plan.organization_name}</span>
            </div>
          </div>
        </th>
        {/* <td>{plan.domain}</td> */}
        <td>{plan.created_domain}</td>
        <td>{plan.organization_reg_no}</td>
        <td>
          <span className="badge badge-dot mr-4">
            <i className="bg-warning" /> {plan.name}
          </span>
        </td>
        <td>{plan.email}</td>
        <td>{plan.mobile_number}</td>
        <td>{plan.order_id}</td>
        <td>{plan.transaction_id}</td>
        <td>{plan.status}</td>
        <td>{plan.plan_id}</td>
        <td>{plan.fee}</td>
        <td>{plan.uqNo}</td>
        <td>
          {this.state.viewLogs ? (
            <Fragment>
              
              <div className="card" style={{width: '50rem',
                position: 'absolute',
                right: '200px',
                top: '-200px'
          }}>

  <div className="card-body">
  
    <h5 className="card-title">
    <span className="avatar rounded-circle mr-3 bg-warning">
              {plan.organization_name.charAt(0)}
            </span>
            <span className="mb-0 text-sm">{plan.organization_name}</span>

            </h5>
    <div className="card-text">
    <pre>{JSON.stringify(plan.logger || {}, null, 2)}</pre>
    </div>
    <button
                className="btn btn-outline-warning"
                onClick={() => this.toggleViewLogs()}
              >
                Hide Logs
              </button>
  </div>
</div>
              {/* <pre>{JSON.stringify(plan.logger || {}, null, 2)}</pre> */}
              
            </Fragment>
          ) : (
            <button
              className="btn btn-outline-info"
              onClick={() => this.toggleViewLogs()}
            >
              View Logs
            </button>
          )}
        </td>
      </tr>
    )
  }
}
export default connect(
  null,
  {
    push
  }
)(Order)



