export function createFeature(planId, featureData) {
  return {
    type: CREATE_FEATURE,
    payload: {},
    meta: {
      type: 'api',
      url: `${API}plans/${planId}`,
      method: 'put',
      body: JSON.stringify({
        features: [featureData]
      })
    },
    success: {
      action: response => createFeatureSuccess(response)
      // redirectTo: '/admin/home/features',
    }
  }
}

export function createFeatureSuccess(createdFeatureData) {
  return {
    type: CREATE_FEATURE_SUCCESS,
    payload: createdFeatureData
  }
}

