export function fetchCoreFeatures() {
  return {
    type: FETCH_CORE_FEATURES,
    payload: {},

    meta: {
      type: 'api',
      url: `${API}features`
    },
    success: {
      action: response => fetchCoreFeaturesSuccess(response)
    }
  }
}

export const fetchCoreFeaturesSuccess = features => {
  return {
    type: FETCH_CORE_FEATURES_SUCCESS,
    payload: features
  }
}

export const setActiveFeature = feature => ({
  type: SET_ACTIVE_FEATURE,
  payload: feature
})
export const clearActiveFeature = () => ({
  type: CLEAR_ACTIVE_FEATURE,
  payload: {}
})



