import React from 'react'
import { connect } from 'react-redux'
import { push } from 'connected-react-router'
import './plan.styles.css'

class Plan extends React.Component {
  constructor(props) {
    super(props)
    this.navigateToDetailPage = this.navigateToDetailPage.bind(this)
  }

  navigateToDetailPage(plan) {
    if (!plan.deleted_at) {
      this.props.push(`/admin/home/plans/${plan.id}`)
    }
  }
  render() {
    const { plan } = this.props
    return (
      <tr className={plan.deleted_at ? 'plan-deleted-status' : ''}>
        <th scope="row">
          <div className="media align-items-center">
            <span className="avatar rounded-circle mr-3 bg-warning">
              {plan.name.charAt(0)}
            </span>
            <div
              className={`media-body ${plan.deleted_at ? '' : 'cursor'}`}
              onClick={() => this.navigateToDetailPage(plan)}
            >
              <span className="mb-0 text-sm">{plan.name}</span>
            </div>
          </div>
        </th>
        <td>₹{plan.price}</td>
        <td>
          <span className="badge badge-dot mr-4">
            <i className="bg-warning" />{' '}
            {plan.deleted_at ? 'Deleted' : 'Active'}
          </span>
        </td>
        <td>1</td>
        <td>1</td>
        <td className="text-right">
          {!plan.deleted_at && (
            <div className="dropdown">
              <a
                className="btn btn-sm btn-icon-only text-light"
                href="#"
                role="button"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                <i className="fas fa-ellipsis-v" />
              </a>
              <div className="dropdown-menu dropdown-menu-right dropdown-menu-arrow">
                <a
                  onClick={() => this.navigateToDetailPage(plan)}
                  className="dropdown-item"
                >
                  View
                </a>
                <a
                  className="dropdown-item"
                  onClick={() => this.props.deletePlan(plan.id)}
                >
                  Delete
                </a>
              </div>
            </div>
          )}
        </td>
      </tr>
    )
  }
}
export default connect(
  null,
  {
    push
  }
)(Plan)



