import {
  SET_PLAN_DETAILS,
  CLEAR_PLAN_DETAILS,
  CREATE_FEATURE_SUCCESS
} from '../types'

export const initialState = {}
export const reducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_PLAN_DETAILS:
      return action.payload.data
    case CLEAR_PLAN_DETAILS:
      return action.payload
    case CREATE_FEATURE_SUCCESS:
      return { ...state, features: action.payload.data }
    default:
      return state
  }
}
