import { CREAT_PLAN, FETCH_PLANS_SUCCESS, CREAT_PLAN_SUCCESS, FETCH_PLANS, DELETE_PLAN, DELETE_PLAN_SUCCESS } from '../types';
import { API } from '../../constants';

export function fetchPlans() {
  // const request = axios.get(`${API}plans`);
  return {
    type: FETCH_PLANS,
    payload: {},

    meta: {
      type: 'api',
      // url: host + '/' + timezone + '/' + str + '.json'
      url: `${API}plans`
    },
    success: {

      action: (response) => fetchPlansSuccess(response)
    }
  };
}

export const fetchPlansSuccess = (retrievedPlansData) => {
  return {
    type: FETCH_PLANS_SUCCESS,
    payload: retrievedPlansData
  }
}

export function createPlan(planData) {
  // const request = axios.post(`${API}plans`, planData);
  return {
    type: CREAT_PLAN,
    payload: {},
    meta: {
      type: 'api',
      url: `${API}plans`,
      method: 'post',
      // headers: { 'Content-Type': 'application/json' },

      body: JSON.stringify(planData)
    },
    success: {
      action: (response) => createPlanSuccess(response),
      redirectTo: '/admin/home/plans',
    }
  };
}

export function createPlanSuccess(createdPlanData) {
  // const request = axios.post(`${API}plans`, planData);
  return {
    type: CREAT_PLAN_SUCCESS,
    payload: createdPlanData,
  };
}

export const deletePlan = (plan_id) => ({
  type: DELETE_PLAN,
  payload: {},
  meta: {
    type: 'api',
    url: `${API}plans/${plan_id}`,
    method: 'delete',
    // headers: { 'Content-Type': 'application/json' },

    // body: JSON.stringify(plan_id)
  },
  success: {
    action: (response) => deletePlanSuccess(response),
  }

})

export const deletePlanSuccess = (planId) => ({
  type: DELETE_PLAN_SUCCESS,
  payload: planId
})






