import React, { Fragment } from 'react'

export const RenderField = ({ disabled = false, label, placeholder, input, type, meta: { touched, error } }) => (
  <Fragment>
    <label>{label}</label>
    {/* has-success */}
    <div className={`form-group ${touched && error ? 'has-danger' : ''}`}>
      {/* is-valid */}
      <input
        className={`form-control form-control-alternative ${
          touched && error ? 'is-invalid' : ''
          }`}
        {...input}
        placeholder={placeholder || label}
        component={type}
        disabled={disabled}
      />
      {touched &&
        (error && (
          <div className="invalid-feedback" style={{ display: 'block' }}>
            {error}
          </div>
        ))}
    </div>
  </Fragment>
)

export const RenderRadio = ({ label, input, htmlFor, type, meta: { touched, error } }) => (
  <Fragment>
    {/* has-success */}



    <div className={`form-group ${touched && error ? 'has-danger' : ''}`}>
      {/* is-valid */}
      <div className="custom-control custom-radio mb-3">
        <input
          className={`form-control form-control-alternative custom-control-input ${
            touched && error ? 'is-invalid' : ''
            }`}
          {...input}
          placeholder={label}
          component={type}
          type="radio"
          id={htmlFor}
        />

        <label className="custom-control-label" htmlFor={htmlFor}>{label}</label>
      </div>
      {touched &&
        (error && (
          <div className="invalid-feedback" style={{ display: 'block' }}>
            {error}
          </div>
        ))}
    </div>
  </Fragment>
)

// for radio group
// https://github.com/erikras/redux-form/issues/124

export const RenderSelect = ({ disabled = false, label, placeholder, input, type, meta: { touched, error }, children }) => (
  <Fragment>
    <label>{label}</label>
    {/* has-success */}
    <div className={`form-group ${touched && error ? 'has-danger' : ''}`}>
      {/* is-valid */}
      <select
        className={`form-control form-control-alternative ${
          touched && error ? 'is-invalid' : ''
          }`}
        {...input}
        placeholder={placeholder || label}
        component={type}
        disabled={disabled}
      >
        {children}
      </select>
      {touched &&
        (error && (
          <div className="invalid-feedback" style={{ display: 'block' }}>
            {error}
          </div>
        ))}
    </div>
  </Fragment>
)



