import { combineReducers } from 'redux'
import { reducer as formReducer } from 'redux-form'
import { connectRouter } from 'connected-react-router'

import * as plans from './plans.reducer'
import * as plan from './plan.reducer'
import * as features from './features.reducer'
import * as orders from './orders.reducer'

export const rootReducer = history =>
  combineReducers({
    router: connectRouter(history),
    form: formReducer,
    plans: plans.reducer,
    plan: plan.reducer,
    features: features.reducer,
    orders: orders.reducer
  })

export const initialState = {
  plans: plans.initialState,
  plan: plan.initialState,
  features: features.initialState,
  orders: orders.initialState
}

export default rootReducer



