import React from 'react'

export const TYPES = {
    PRIMARY: 'primary',
    WARNING: 'warning',
    DANGER: 'danger',
    SUCCESS: 'success',
}

export const SIZES = {
    SMALL: 'small',
    MEDIUM: 'medium',
    LARGE: 'large'
}

const BaseButton = ({
    text = 'Submit',
    onClick = () => {
        console.log('submit button clicked ! ');
    },
    type = 'submit',
    disabled = false,
    buttonType = TYPES.PRIMARY,
    buttonSize = SIZES.MEDIUM,
    btnClassNames = []
}) => (
        <button
            type={type}
            disabled={disabled}
            onClick={onClick}
            className={[buttonSize, buttonType, ...btnClassNames].join(' ')}
        >
            {text}
        </button>
    );

export const Primary = props => (
    <BaseButton {...props} buttonType={TYPES.PRIMARY} />
);
export const Warning = props => (
    <BaseButton {...props} buttonType={TYPES.WARNING} />
);
export const Danger = props => (
    <BaseButton {...props} buttonType={TYPES.DANGER} />
);
export const Success = props => (
    <BaseButton {...props} buttonType={TYPES.SUCCESS} />
);



