import React, { Fragment } from 'react'
import { Field, reduxForm, formValueSelector } from 'redux-form'
import { connect } from 'react-redux'
import { createFeature, fetchCoreFeatures } from '../../store/actions'
import {
  RenderField,
  RenderRadio,
  RenderSelect
} from '../../components/RenderField'

class FeatureForm extends React.Component {
  componentDidMount() {}

  handleInitialize() {
    const initData = this.props.selectedFeature
    this.props.initialize({
      name: initData.name,
      description: initData.description,
      featureType: initData.type,
      code: initData.code
    })
  }

  onSubmit(values) {
    if ((this.props.selectedFeature || {}).id) {
      // TODO update feature details
    } else {
      this.props.createFeature(this.props.plan.id, {
        ...values,
        type: values.featureType
      })
      this.closeModal()
    }
  }

  closeModal() {
    $('#add-or-edit-feature-modal').modal({
      show: false
    })
  }

  render() {
    const {
      handleSubmit,
      plan,
      selectedFeature,
      restrictLimit,
      coreFeatures = []
    } = this.props

    const submitText = (selectedFeature || {}).id ? 'Update' : 'Add'
    if (selectedFeature.id) {
      this.handleInitialize()
    }
    return (
      <Fragment>
        <form onSubmit={handleSubmit(this.onSubmit.bind(this))}>
          <div className="row">
            <div className="col-md-6">
              <Field
                name="name"
                component={RenderField}
                type="text"
                placeholder="Name of the Feature"
                label="Title"
              />
            </div>
            <div className="col-md-6">
              <Field
                name="description"
                component={RenderField}
                type="text"
                placeholder="description of the Feature"
                label="description"
              />
            </div>
          </div>
          <div className="row">
            <div className="col-md-6">
              <div className="row px-2 mb-3">
                <div className="col-md-12">Type of feature</div>
              </div>
              <div className="row">
                <div className="col-md-6">
                  <Field
                    name="featureType"
                    component={RenderRadio}
                    type="radio"
                    label="Feature"
                    htmlFor="two"
                    value="feature"
                  />
                </div>
                <div className="col-md-6">
                  <Field
                    name="featureType"
                    component={RenderRadio}
                    type="radio"
                    label="Limit"
                    htmlFor="one"
                    value="limit"
                  />
                </div>
              </div>
            </div>
            <div className="col-md-6">
              <Field
                name="limit"
                component={RenderField}
                type="text"
                placeholder="Limit"
                label="Limit"
                disabled={restrictLimit}
              />

              {/* {captureLimit && (
                <Field
                  name="limit"
                  component={RenderField}
                  type="text"
                  placeholder="Limit"
                  label="Limit"
                />
              )} */}
            </div>
          </div>
          <div className="row">
            <div className="col-md-12">
              {/* <label>Favorite Color</label>
              <div className="form-group">
                <Field
                  name="code"
                  component="select"
                  placeholder="Core Feature"
                  label="Core Feature"
                  type="select"
                  className="form-control"
                >
                  <option value="" />
                  {coreFeatures.map(eFeature => {
                    return (
                      <option key={eFeature.id} value={eFeature.id}>
                        {eFeature.name}
                      </option>
                    )
                  })}
                </Field>
              </div> */}
              <Field
                name="code"
                component={RenderSelect}
                placeholder="Core Feature"
                label="Core Feature"
                type="select"
                className="form-control"
              >
                <option value="" />
                {coreFeatures.map(eFeature => {
                  return (
                    <option key={eFeature.id} value={eFeature.id}>
                      {eFeature.name}
                    </option>
                  )
                })}
              </Field>
            </div>
          </div>
          <div>
            <button type="submit" className="btn btn-primary">
              {submitText}
            </button>
            {!!(selectedFeature || {}).id && (
              <button
                id="close-modal"
                type="submit"
                className="btn btn-danger"
                data-dismiss="modal"
              >
                cancel
              </button>
            )}
          </div>
        </form>
      </Fragment>
    )
  }
}

const validate = values => {
  const errors = {}

  if (!values.name) {
    errors.name = 'name is required'
  } else if (values.name.length < 5) {
    errors.name = 'Must be 5 characters or more'
  }
  if (!values.description) {
    errors.description = 'description is required'
  }
  if (!values.featureType) {
    errors.featureType = 'Please select feature type'
  } else if (values.featureType === 'limit') {
    if (!values.limit) {
      errors.limit = 'Please specify limit'
    }
  }

  if (!values.code) {
    errors.code = 'Please select core feature'
  } else if ((values.code || '').trim().length <= 0) {
    errors.code = 'Please select core feature'
  }
  return errors
}

const selector = formValueSelector('featureForm')

export default reduxForm({
  form: 'featureForm',
  validate
})(
  connect(
    state => {
      const featureType = selector(state, 'featureType')
      return {
        restrictLimit: featureType !== 'limit',
        coreFeatures: state.features.coreFeatures || [],
        plan: state.plan,
        selectedFeature: state.features.selectedFeature
      }
    },
    { createFeature, fetchCoreFeatures }
  )(FeatureForm)
)



