import React, { Fragment, Component } from 'react'
import { connect } from 'react-redux'
import {
  fetchPlans,
  deletePlan,
  clearPlanDetails,
  fetchCoreFeatures
} from '../../store/actions'
import { Link } from 'react-router-dom'
import Feature from './Feature'
import FeatureForm from './FeatureForm'

class FeatureList extends Component {
  constructor(props) {
    super(props)
  }
  componentDidMount() {
    // this.props.clearPlanDetails();
    // this.props.fetchPlans()
    // Note: make sure if the below data once loaded, donot refetch
    if (!this.props.features.length) {
      this.props.fetchCoreFeatures()
    }
  }

  preparePlansList() {
    const { plan } = this.props
    return (plan.features || []).map(feature => (
      <Feature key={feature.id} feature={feature} />
    ))
  }

  addOrEditModalDialog() {
    const { feature = {} } = this.props

    return (
      <div className="col-md-4">
        <div
          className="modal fade"
          id="add-or-edit-feature-modal"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="add-or-edit-feature-modal"
          aria-hidden="true"
        >
          <div
            className="modal-dialog modal- modal-dialog-centered modal-lg"
            role="document"
          >
            <div className="modal-content">
              <div className="modal-body p-0">
                <div className="card bg-secondary shadow border-0">
                  <div className="card-body px-lg-5 py-lg-5">
                    <div className="text-center text-muted mb-4">
                      <small>Edit {feature.name} details</small>
                    </div>
                    <FeatureForm />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    )

    //  Modal content ends here
  }

  render() {
    return (
      <Fragment>
        {this.addOrEditModalDialog()}

        <div className="">
          <div className="row">
            <div className="card shadow">
              <div className="card-header border-0">
                <div className="row">
                  <div className="col-12 col-md-8">
                    <span>
                      <h3 className="mb-0">Features</h3>
                    </span>
                  </div>
                  <div className="col-6 col-md-4">
                    <span>
                      <button
                        type="button"
                        className="btn btn-sm btn-warning ml-4"
                        data-toggle="modal"
                        data-target="#add-or-edit-feature-modal"
                      >
                        Add New Feature
                      </button>
                    </span>
                  </div>
                </div>
              </div>
              <div className="table-responsive">
                <table className="table align-items-center">
                  <thead className="thead-light">
                    <tr>
                      <th scope="col">Name</th>
                      <th scope="col">type (Limit)</th>
                      <th scope="col">Status</th>
                      <th scope="col">description</th>
                      <th scope="col">
                        code (core feature)
                        {/* from database seeds */}
                      </th>
                      <th scope="col" />
                    </tr>
                  </thead>
                  <tbody>{this.preparePlansList()}</tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </Fragment>
    )
  }
}
export default connect(
  state => ({
    plan: state.plan,
    features: state.features.coreFeatures || []
  }),
  { fetchPlans, deletePlan, clearPlanDetails, fetchCoreFeatures }
)(FeatureList)



