import React, { Fragment, Component } from 'react'
import { connect } from 'react-redux'
import { fetchOrders } from '../../store/actions'
import './order.styles.css'
import Order from './Order'
import { Link } from 'react-router-dom'

class OrderList extends Component {
  constructor(props) {
    super(props)
  }
  componentDidMount() {
    // this.props.clearPlanDetails()
    this.props.fetchOrders()
    // // Note: make sure if the below data once loaded, donot refetch
    // if (!this.props.features.length) {
    //   this.props.fetchCoreFeatures()
    // }
    // this.handleClick = this.handleClick.bind(this)
  }

  preparePlansList() {
    return (this.props.orders || []).map(order => (
      <Order key={order.id} plan={order} />
    ))
  }

  handleClick(pageNumber) {
    this.props.fetchOrders(pageNumber)
  }

  render() {
    // https://stackoverflow.com/questions/40232847/how-to-implement-pagination-in-reactjs
    const { total, currentPage, perPage, lastPage } = this.props

    // Logic for displaying page numbers
    const pageNumbers = []
    for (let i = 1; i <= Math.ceil(total / perPage); i++) {
      pageNumbers.push(i)
    }

    const renderPageNumbers = pageNumbers.map(pageNumber => {
      return (
        <li
          className={`page-item ${
            pageNumber === this.props.currentPage ? 'active' : ''
          }`}
          key={pageNumber}
          id={pageNumber}
          onClick={() => this.handleClick(pageNumber)}
        >
          <a className="page-link">{pageNumber}</a>
        </li>
      )
    })

    const renderPagination = (
      <Fragment>
        <li className={`page-item  ${currentPage === 1 ? 'disabled' : ''}`}>
          <a
            className="page-link"
            tabIndex="-1"
            onClick={() => this.handleClick(1)}
          >
            <i className="fas fa-angle-left" />
            <span className="sr-only">Previous</span>
          </a>
        </li>
        {renderPageNumbers}
        <li
          className={`page-item  ${currentPage === lastPage ? 'disabled' : ''}`}
        >
          <a className="page-link" onClick={() => this.handleClick(lastPage)}>
            <i className="fas fa-angle-right" />
            <span className="sr-only">Next</span>
          </a>
        </li>
      </Fragment>
    )

    // return <Fragment>
    //   <li className="page-item disabled">
    //                 <a className="page-link" href="#" tabIndex="-1">
    //                   <i className="fas fa-angle-left" />
    //                   <span className="sr-only">Previous</span>
    //                 </a>
    //               </li>

    //               <li className="page-item">
    //                 <a className="page-link" href="#">
    //                   <i className="fas fa-angle-right" />
    //                   <span className="sr-only">Next</span>
    //                 </a>
    //               </li>
    // </Fragment>

    return (
      <Fragment>
        <div className="container-fluid mt--7">
          <div className="row">
            <div className="card shadow">
              <div className="card-header border-0">
                <div className="row">
                  <div className="col-12 col-md-8">
                    <span>
                      <h3 className="mb-0">Orders</h3>
                    </span>
                  </div>
                  <div className="col-6 col-md-4">
                    <span>
                      {/* <Link to={'/admin/home/plans/create'}>
                        <button
                          type="button"
                          className="btn btn-primary btn-sm add-plan"
                        >
                          Add New Plan
                        </button>
                      </Link> */}
                    </span>
                  </div>
                </div>
              </div>
              <div className="table-responsive">
                <table className="table align-items-center">
                  <thead className="thead-light">
                    <tr>
                      <th scope="col">Organization Name</th>
                      <th scope="col">Organization Domain</th>
                      <th scope="col">Organization Reg No</th>
                      <th scope="col">Name</th>
                      <th scope="col">Email</th>
                      <th scope="col">Mobile</th>
                      <th scope="col">order_id</th>
                      <th scope="col">transaction_id</th>
                      <th scope="col">status</th>
                      <th scope="col">plan_id</th>
                      <th scope="col">fee</th>
                      <th scope="col">uqNo</th>
                      <th scope="col">logger</th>

                      <th scope="col" />
                    </tr>
                  </thead>
                  <tbody>{this.preparePlansList()}</tbody>
                </table>
              </div>

              <div className="card-footer py-4">
                <nav aria-label="">
                  <ul className="pagination justify-content-end mb-0">
                    {/* {renderPageNumbers} */}
                    {renderPagination}
                  </ul>
                </nav>
              </div>
            </div>
          </div>
        </div>
      </Fragment>
    )
  }
}
export default connect(
  ({ orders }) => ({
    orders: orders.data,
    currentPage: orders.current_page,
    perPage: orders.per_page,
    total: orders.total,
    lastPage: orders.last_page
  }),
  { fetchOrders }
)(OrderList)



