import React, { Fragment } from 'react'
import { Field, reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import { createPlan } from '../../store/actions'

const renderField = ({ label, input, type, meta: { touched, error } }) => (
  <Fragment>
    <label>{label}</label>
    {/* has-success */}
    <div className={`form-group ${touched && error ? 'has-danger' : ''}`}>
      {/* is-valid */}
      <input
        className={`form-control form-control-alternative ${
          touched && error ? 'is-invalid' : ''
          }`}
        {...input}
        placeholder={label}
        component={type}
      />
      {touched &&
        (error && (
          <div className="invalid-feedback" style={{ display: 'block' }}>
            {error}
          </div>
        ))}
    </div>
  </Fragment>
)

class PlanForm extends React.Component {

  componentDidMount() {
    this.handleInitialize()
  }

  handleInitialize() {
    const initData = this.props.plan
    this.props.initialize(initData)
  }

  onSubmit(values) {
    if ((this.props.plan || {}).id) {
      // TODO update plan details
    } else {
      this.props.createPlan(values)
    }
  }

  render() {
    const { handleSubmit, plan } = this.props
    const submitText = (plan || {}).id ? 'Update' : 'Create'
    return (
      <Fragment>
        <form onSubmit={handleSubmit(this.onSubmit.bind(this))}>
          <div className="row">
            <div className="col-md-6">
              <Field
                name="name"
                component={renderField}
                type="text"
                placeholder="Name of the Plan"
                label="Title"
              />
            </div>
            <div className="col-md-6">
              <Field
                name="description"
                component={renderField}
                type="text"
                placeholder="description of the Plan"
                label="description"
              />
            </div>
          </div>
          <Field
            name="price"
            component={renderField}
            type="text"
            placeholder="price of the Plan"
            label="price"
          />

          <Field
            name="currency"
            component={renderField}
            type="text"
            placeholder="currency of the Plan"
            label="currency"
          />

          <Field
            name="duration"
            component={renderField}
            type="text"
            placeholder="duration of the Plan"
            label="duration"
          />
          <div>
            <button type="submit" className="btn btn-primary">
              {submitText}
            </button>
            {!!(plan || {}).id && (
              <button type="submit" className="btn btn-danger" data-dismiss="modal">
                cancel
              </button>
            )}
          </div>
        </form>
      </Fragment>
    )
  }
}

const validate = values => {
  const errors = {}

  if (!values.name) {
    errors.name = 'name is required'
  } else if (values.name.length < 5) {
    errors.name = 'Must be 5 characters or more'
  }
  if (!values.description) {
    errors.description = 'description is required'
  }
  if (!values.price) {
    errors.price = 'price is required'
  }
  if (!values.currency) {
    errors.currency = 'currency is required'
  }
  if (!values.duration) {
    errors.duration = 'duration is required'
  }
  return errors
}

export default reduxForm({
  form: 'newPlanForm',
  validate
  // enableReinitialize: true
})(
  connect(
    state => {
      return {
        plan: state.plan
      }
    },
    { createPlan }
  )(PlanForm)
)

